<script>
import Navbar from "@/components/Navbar.vue";
import Sidebar from "@/components/Sidebar.vue";
import 'leaflet/dist/leaflet.css';
export default {

  components:{
    Navbar,
    Sidebar,

  },
  data(){
    return{
      slides: 7,
      museum: [],
      zoom: 14,
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',

      objects_views:[],
      attribution:
          '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',

      center: [51.505, -0.159],
      markerLatLng: [51.504, -0.159]

    };
  },
  mounted() {
    this.$http.get(`objects_main/${this.$route.query.id}/`,{
      headers: {
        Authorization: `Bearer ${localStorage.getItem('token')}`,
      },
    })
    .then(res=>{
      this.objects_views=res.data
      this.center=[res.data.latitude,res.data.longitude]
      this.markerLatLng=[res.data.latitude,res.data.longitude]
      this.loadYandexMap(res.data.latitude, res.data.longitude);
    })
  },
    methods: {
    loadYandexMap(lat, lng) {
      if (window.ymaps) {
        // API allaqachon yuklangan bolsa, xaritani yaratish
        this.initMap(lat, lng);
      } else {
        // API yuklanmagan bolsa, yuklaymiz
        const script = document.createElement("script");
        script.src = `https://api-maps.yandex.ru/2.1/?apikey=c0abfed4-3b4b-463c-8460-6d71b6ee6e70&lang=ru_RU`;
        script.onload = () => this.initMap(lat, lng);
        document.head.appendChild(script);
      }
    },
    initMap(lat, lng) {
      window.ymaps.ready(() => {
        const map = new window.ymaps.Map("map", {
          center: [lat, lng],
          zoom: 14,
        });
        map.geoObjects.add(new window.ymaps.Placemark([lat, lng]));
      });
    }
  }
}
</script>
