                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Obyekt tasnifi
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.classification}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
