                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.residential}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Obyekt davri
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.obj_period}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Viloyat
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.region_name}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Tuman
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.district_name}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Manzili
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.address}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Kadastr raqami
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.cadastr}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Kenglik
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.latitude}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Uzunligi
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.longitude}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Ko'chmas mulk huquqi
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.estate_law}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Balansda saqlovchi / tasarruf etuvchi nomi to'liq
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.custodian_name}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Umumiy yer maydoni
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.land_area}}
                              </div>
                            </div>

                            <div class="table_border col-xl-6">
                              <div>
                                Gaz (bor/yo'q)
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                <span v-if="item.natural_gas=='yes'">Ha</span>
                                <span v-if="item.natural_gas=='no'">Yo'q</span>
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Elektr(bor/yo'q)
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                <span v-if="item.electricity=='yes'">Ha</span>
                                <span v-if="item.electricity=='no'">Yo'q</span>
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Ijaraga berilgan (bor/yo'q)
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.address}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Ijara shartnoma soni
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.contracts}}
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                Muhofaza tegrasi belgilanganligi
                              </div>
                            </div>
                            <div class="table_border col-xl-6">
                              <div>
                                {{item.protective}}
                              </div>
                            </div>
                            <div class="p-2 table_border col-xl-12">
                              <div class="row">
                                <div class="col-xl-3" v-for="pho in item.photos" :key="pho">
                                  <img width="100%" class="my-2" :src="pho" alt="">
                                </div>
                              </div>

                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </b-collapse>

        </div>
      </div>
      <div class="main_button">
        <div>
          <b-pagination
              v-model="page"
              :total-rows="objects_views.total_pages"
              :per-page="currentPage"
              first-number
              last-number
              aria-controls="my-table"></b-pagination>
        </div>
        <div>
          <h2>Jami:{{objects_views.total_counts}}</h2>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Sidebar from "@/components/Sidebar.vue";
import Navbar from "@/components/Navbar.vue";
import Destricts from '@/Json/districts.json'
import Regions from '@/Json/regions.json'
import Kvartals from '@/Json/kvartals.json'
import QrcodeVue from 'qrcode.vue'
import html2pdf from 'html2pdf.js'
export default {
  components:{
    Navbar,
    Sidebar,
    QrcodeVue
  },
  data() {
    return {
      perPage: 3,
      page: 1,
      menu:'',
      pageSize: 10,
      // perPage: 15,
      currentPage: 1,
      items: [

      ],
      size:400,
      search:'',
      stats:[],
      sportsman:[],

      selected: [], // Must be an array reference!
      options: [
      ],
      gender:'Erkak',
      name:'',
      district_name:'',
      content:'',
      date:new Date(),
      options1: [
      ],
      options2: [
      ],
      options3: [
      ],
      options4: [
        {
          code:1,
          name:"Hokim sektori"
        },
        {
          code:2,
          name:"Prokuror sektori"
        },
        {
          code:3,
          name:"Ichki Ishlar sektori"
        },
        {
          code:4,
          name:"Davlat Soliq sektori"
        }
      ],
      options5: [
        {
          code:1,
          name:"Workout"
        },
        {
          code:2,
          name:"Stritbol"
        },
        {
          code:3,
          name:"Voleybol"
        },
        {
          code:4,
          name:"Shashka"
        },
        {
          code:5,
          name:"Stol tennis"
        },
        {
          code:6,
          name:"Yengil atletika"
        },
      ],

      options6: [
        {
          code:1,
          name:"7-10"
        },
        {
          code:2,
          name:"11-15"
        },
        {
          code:3,
          name:"16-21"
        },
        {
          code:4,
          name:"22-26"
        },
        {
          code:5,
          name:"27-30"
        },
      ],
      value: 'https://diplom.minsport.uz?id=SRVF00000001',
      options7:[],

      options8: [
        {
          code:1,
          name:"O'g'il"
        },
        {
          code:2,
          name:"Qiz"
        },
      ],
      objectItem: {},
      objectItem1: {},
      objectItem2: {},
      objectItem3: {},
      objectItem4: {},
      objectItem5: {},
      objectItem6: {},
      objectItem7: {},
      stringItem: '',
      regions: [],
      minDate: '1950',
      maxDate: '2040',
      isFilt:'',
      filTname:'',
      objects_views:[],
      user_id:'',
      user_info:''
    }
  },
  mounted() {
    this.city=Regions
    this.region=Destricts
    this.destricts=Kvartals


    Regions.forEach(el=>{
      this.options1.push({
        code:el.id,
        name:el.name
      })
    })

    this.$http.get(`user-me/`,{
      headers: {
        Authorization: `Bearer ${localStorage.getItem('token')}`,
      },
    })
        .then(res=>{
          this.user_info=res.data
          this.user_id=res.data.id
          if (this.user_id==1)
          {
            this.$http.get(`objects/?p=${this.page}`,{
              headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`,
              },
            })
                .then(res=>{
                  this.objects_views=res.data
                })
          }
          else {

            this.$http.get(`objects/?user_id=${this.user_id}&&p=${this.page}`,{
              headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`,
              },
            })
                .then(res=>{
                  this.objects_views=res.data
                })
          }

        })


  },
  methods: {
    exportToPDF (index) {
      console.log(this.$refs.document)
      html2pdf(this.$refs.items[index], {
        margin: 1,
        filename: 'document.pdf',
        image: { type: 'jpeg', quality: 0.98 },
        html2canvas: { dpi: 192, letterRendering: true },
        jsPDF: { unit: 'mm', format: 'a4', orientation: 'portrait', }
      })
    },
    generateReport1() {
      this.$refs.html2Pdf.generatePdf();
    },
    generatePdf() {
      if (this.$refs.html2Pdf && typeof this.$refs.html2Pdf.generatePdf === 'function') {
        this.$refs.html2Pdf.generatePdf();
      } else {
        console.error("generatePdf is not a function on this.$refs.html2Pdf");
      }
    },

    getregion(){
      console.log(this.objectItem)
      this.options2=[]
      this.options3=[]

      Destricts.filter(tree => tree.parent === this.objectItem.code).forEach(el=>{
        this.options2.push({
          code:el.id,
          name:el.name
        })
      })
      this.$http.get(`objects/?regions_name=${this.objectItem.name}`,{
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
          .then(res=>{
            this.objects_views=res.data
          })

    },
    exportExe(){
      this.$http.get(`export-products-to-excel/`)
    },
    getkvartals(){
      Kvartals.filter(tree => tree.parent === this.objectItem1.code).forEach(el=>{
        this.options3.push({
          code:el.id,
          name:el.name
        })
      })

      this.$http.get(`objects/?regions=${this.objectItem.code}&&district=${this.objectItem1.code}`,{
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
          .then(res=>{
            this.objects_views=res.data
          })
    },
    getDistrict(){

      this.$http.get(`objects/?regions=${this.objectItem.code}&&address_id=${this.objectItem1.code}&&address_id=${this.objectItem2.code}`,{
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
          .then(res=>{
            this.objects_views=res.data
          })

    },

    searchFilt(){

      if(this.search!=undefined){
        this.filTname=this.filTname+`&name=${this.search}`
      }
      this.$axiosDefault.get(`v1/api/sportsman?${this.filTname}`)
          .then(res=>{
            this.sportsman=res.data
          })
    },

    clearFunc(){
      this.$http.get('objects/',{
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
          .then(res=>{
            this.objects_views=res.data
            this.objectItem1=[]
            this.objectItem=[]
            this.objectItem2=[]
          })
    },
    deletes(item){
      let isBoss = confirm("You really want to delete?");
      if (isBoss == true) {
        this.$http.delete(`/objects/${item}/`,
            {
              headers: {
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
                'Accept-Language': 'uz-latin'
              }
            })
            .then(()=>{

              this.$toast(" ", {
                timeout: 2000,
                type:"success"
              });
              this.$http.get(`user-me/`,{
                headers: {
                  Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
              })
                  .then(res=>{
                    this.user_id=res.data.id
                    if (this.user_id==1)
                    {
                      this.$http.get(`objects/?p=${this.page}`,{
                        headers: {
                          Authorization: `Bearer ${localStorage.getItem('token')}`,
                        },
                      })
                          .then(res=>{
                            this.objects_views=res.data
                          })
                    }
                    else {

                      this.$http.get(`objects/?user_id=${res.data.id}`,{
                        headers: {
                          Authorization: `Bearer ${localStorage.getItem('token')}`,
                        },
                      })
                          .then(res=>{
                            this.objects_views=res.data
                          })
                    }

                  })
            })

      }
    }
  },
  watch: {
    page(page) {

      this.$http.get(`user-me/`,{
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
          .then(res=>{
            this.user_id=res.data.id
            if (this.user_id==1)
            {
              this.$http.get(`objects/?page=${page}`,{
                headers: {
                  Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
              })
                  .then(res=>{
                    this.objects_views=res.data
                  })
            }
            else {

              this.$http.get(`objects/?user_id=${this.user_id}&page=${page}`,{
                headers: {
                  Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
              })
                  .then(res=>{
                    this.objects_views=res.data
                  })
            }

          })


    }
  },
}
</script>
