<template>
  <div>
    <div class="m_sidebars">
      <Sidebar></Sidebar>
      <Navbar></Navbar>
    </div>

    <div class="maps_father">
      <div id="map" style="width: 100%; height: 100%"></div>
    </div>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Sidebar from "@/components/Sidebar.vue";
import "leaflet/dist/leaflet.css";

export default {
  components: {
    Navbar,
    Sidebar,
  },
  data() {
    return {
      zoom: 14,
      coordinates: [],
    };
  },
  mounted() {
    this.fetchObjects();
  },
  methods: {
    fetchObjects() {
      this.$http
        .get("objects/", {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        })
        .then((res) => {
          console.log("API response:", res.data.data); //  Ma'lumotni tekshirish
          if (Array.isArray(res.data.data)) {
            this.coordinates = res.data.data.map((el) => [el.latitude, el.longitude]);
          } else if (res.data.data && Array.isArray(res.data.data)) {
            this.coordinates = res.data.data.map((el) => [el.latitude, el.longitude]);
          } else {
            console.error("Unexpected data format:", res.data.data);
            this.coordinates = [];
          }
          this.loadYandexMap();
        })
        .catch((err) => console.error("Error fetching objects:", err));
    },
    loadYandexMap() {
      if (window.ymaps) {
        this.initMap();
      } else {
        const script = document.createElement("script");
        script.src = "https://api-maps.yandex.ru/2.1/?apikey=c0abfed4-3b4b-463c-8460-6d71b6ee6e70&lang=ru_RU";
        script.onload = this.initMap;
        document.head.appendChild(script);
      }
    },
    initMap() {
      window.ymaps.ready(() => {
        const map = new window.ymaps.Map("map", {
          center: [41.2995, 69.2401], // Toshkentni markazga olamiz
          zoom: 6,
        });

        const geoObjects = this.coordinates.map((coord) => {
          return new window.ymaps.Placemark(coord, {
            hintContent: `Koordinata: ${coord[0]}, ${coord[1]}`,
          });
        });

        const clusterer = new window.ymaps.Clusterer({
          preset: "islands#invertedBlueClusterIcons",
          groupByCoordinates: false,
          clusterDisableClickZoom: false,
          clusterHideIconOnBalloonOpen: false,
          geoObjectHideIconOnBalloonOpen: false,
        });

        clusterer.add(geoObjects);
        map.geoObjects.add(clusterer);
      });
    },
  },
};
</script>

<style>
.maps_father {
  height: 100vh;
  z-index: 1;
  position: relative;
  top: 0;
}

.m_sidebars {
  z-index: 2;
  height: 100vh;
  position: absolute;
}
</style>